import 'antd/dist/antd.css';
import React, { useState } from "react";
import { Form, Button, Input, Upload, Modal, notification } from "antd";
import { PlusOutlined, FrownOutlined } from "@ant-design/icons";
import { getBase64FromFile } from "../../utils/Base64";
import { axiosInstance } from "../api";
import parseErrorMessages from "../../utils/forms"


import { useHistory } from "react-router-dom";
export default function PostNewForm() {
  const history = useHistory();
  const [fieldErrors, setFieldErrors] = useState({});
  const [fileList, setFileList] = useState([]);
  const handelUploadChange = ({ fileList }) => {
    //   setFileList  ==>    
    setFileList(fileList);
  };
  const [previewPhoto, setPreviewPhoto] = useState({
    base64: null,
    visible: false,
  }); //   base64

  const handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      //preview  url  getBase64FromFile  
      file.preview = await getBase64FromFile(file.originFileObj);
    }
    //  url preview setPreviewPhoto  .
    setPreviewPhoto({ visible: true, base64: file.url || file.preview });
  };

  const handleFinish = async (fieldValues) => {
    //   ==> subit    .
    const {
      caption,
      location,
      photo: { fileList },
    } = fieldValues;
    const formData = new FormData();
    formData.append("caption", caption);
    formData.append("location", location);

    fileList.forEach((file) => {
      formData.append("photo", file.originFileObj);
    });
    history.push("/community"); //comunity  .
    // const headers = {
    //   Authorization: ` JWT ${JSON.parse(localStorage.getItem("jwtToken"))}`,
    // }; //  JWT 
    // try {
    //   const response = await axiosInstance.post("/api/post/", formData, {
    //     headers,
    //   });
    //   console.log("success response", response);
    //   history.push("/community"); //comunity  .
    // } catch (error) {
    //   if (error.response) {
    //     const { status, data: fieldsErrorMessages } = error.response;
    //     if (typeof fieldsErrorMessages === "string") {
    //       notification.open({
    //         message: " ",
    //         description: ` ${status}    `,
    //         icon: <FrownOutlined style={{ color: "#ff3333" }} />,
    //       });
    //     } else {
    //       setFieldErrors(parseErrorMessages(fieldsErrorMessages));
    //     }
    //   }
    // }
  };

  return (
    <Form
      name="basic"
      labelCol={{
        span: 8,
      }}
      wrapperCol={{
        span: 16,
      }}
      initialValues={{
        remember: true,
      }}
      onFinish={handleFinish}
      autoComplete={"false"}
    >
      
      <Form.Item
        label=" "
        name=" "
        rules={[
          //  Rule antd Form  docs 
          {
            required: true,
            message: "Location ",
          },
        ]}
        hasFeedback
        {...fieldErrors.location}
      >
        <Input />
      </Form.Item>
      <Form.Item
        label=""
        name=""
        rules={[
          //  Rule antd Form  docs 
          {
            required: true,
            message: "Caption ",
          },
        ]}
        hasFeedback
        {...fieldErrors.caption}
        {...fieldErrors.non_field_errors}
      >
        <Input.TextArea />
      </Form.Item>

      {/*      */}
      <Form.Item
        label="Photo"
        name="photo"
        rules={[{ required: true, message: "  " }]}
        hasFeedback
        {...fieldErrors.photo}
      >
        <Upload
          listType="picture-card"
          fileList={fileList}
          beforeUpload={() => {
            return false; //submit     false .
          }}
          onChange={handelUploadChange}
          onPreview={handlePreview}
        >
          {/* {fileList.length > 0 ? null : ( */}
            <div>
              <PlusOutlined />
              <div className="ant-upload-text">Upload</div>
            </div> 
           {/* )} //  1         */}
        </Upload>
      </Form.Item>

      <Form.Item
        wrapperCol={{
          offset: 8,
          span: 16,
        }}
      >
      
        <Button type="primary" htmlType="submit"> 
          Submit
        </Button>
      </Form.Item>
          {/*  htmlType Button   submit    . */}

      <Modal //Modal   ==>preview       .
        visible={previewPhoto.visible}
        footer={null}
        onCancel={() => setPreviewPhoto({ visible: false })} //vislble false    
      >
        <img
          src={previewPhoto.base64}
          style={{ width: "100%" }}
          alt="Preview"
        />
      </Modal>
    </Form>
  );
}